import { useState, useCallback, memo } from 'react'
import { useForm } from '../hooks/useForm'
import { userValidationSchema } from '../utils/validation'
import apiService from '../services/apiService'
import { logger } from '../utils/logger'
import '../styles/Agregar.css'

const CharacterCounter = memo(({ current, max, className = '' }) => {
  const percentage = (current / max) * 100
  let counterClass = 'character-counter'

  if (percentage >= 90) {
    counterClass += ' error'
  } else if (percentage >= 75) {
    counterClass += ' warning'
  }

  return (
    <div className={`${counterClass} ${className}`}>
      {current}/{max}
    </div>
  )
})

CharacterCounter.displayName = 'CharacterCounter'

const FormInput = memo(({
  id,
  name,
  type = 'text',
  label,
  value,
  onChange,
  onBlur,
  error,
  touched,
  placeholder,
  required = false,
  maxLength,
  helpText,
  className = ''
}) => (
  <div className={`form-group ${className}`}>
    <label htmlFor={id} className="form-label">
      {label}
      {required && <span className="required-indicator" aria-hidden="true"> *</span>}
    </label>
    <input
      id={id}
      name={name}
      type={type}
      value={value}
      onChange={onChange}
      onBlur={onBlur}
      className={`form-input ${touched && error ? 'error' : ''}`}
      placeholder={placeholder}
      required={required}
      maxLength={maxLength}
      aria-invalid={touched && error ? 'true' : 'false'}
      aria-describedby={
        [
          touched && error ? `${id}-error` : null,
          helpText ? `${id}-help` : null,
          maxLength ? `${id}-counter` : null
        ].filter(Boolean).join(' ') || undefined
      }
    />
    {maxLength && (
      <CharacterCounter
        current={value?.length || 0}
        max={maxLength}
        className="character-counter"
      />
    )}
    {helpText && (
      <div id={`${id}-help`} className="form-help">
        {helpText}
      </div>
    )}
    {touched && error && (
      <div id={`${id}-error`} className="form-error" role="alert">
        <span aria-hidden="true">⚠️</span>
        {error}
      </div>
    )}
  </div>
))

FormInput.displayName = 'FormInput'

const FormTextarea = memo(({
  id,
  name,
  label,
  value,
  onChange,
  onBlur,
  error,
  touched,
  placeholder,
  maxLength,
  helpText,
  className = ''
}) => (
  <div className={`form-group ${className}`}>
    <label htmlFor={id} className="form-label">
      {label}
    </label>
    <textarea
      id={id}
      name={name}
      value={value}
      onChange={onChange}
      onBlur={onBlur}
      className={`form-textarea ${touched && error ? 'error' : ''}`}
      placeholder={placeholder}
      maxLength={maxLength}
      aria-invalid={touched && error ? 'true' : 'false'}
      aria-describedby={
        [
          touched && error ? `${id}-error` : null,
          helpText ? `${id}-help` : null,
          maxLength ? `${id}-counter` : null
        ].filter(Boolean).join(' ') || undefined
      }
    />
    {maxLength && (
      <CharacterCounter
        current={value?.length || 0}
        max={maxLength}
      />
    )}
    {helpText && (
      <div id={`${id}-help`} className="form-help">
        {helpText}
      </div>
    )}
    {touched && error && (
      <div id={`${id}-error`} className="form-error" role="alert">
        <span aria-hidden="true">⚠️</span>
        {error}
      </div>
    )}
  </div>
))

FormTextarea.displayName = 'FormTextarea'

const Agregar = () => {
  const [successMessage, setSuccessMessage] = useState('')
  const [serverError, setServerError] = useState('')

  const initialValues = {
    usuario: '',
    nombre: '',
    apellido: '',
    ciudad: '',
    direccion: '',
    telefono: '',
    especialidad: '',
    email: '',
    instagram: '',
    facebook: '',
    x: '',
    otro1: '',
    otro2: '',
    otro3: '',
    otro4: ''
  }

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleChange,
    handleBlur,
    handleSubmit,
    reset,
    setFieldError
  } = useForm(initialValues, userValidationSchema)

  const clearMessages = useCallback(() => {
    setSuccessMessage('')
    setServerError('')
  }, [])

  const onSubmit = useCallback(async (formValues) => {
    try {
      clearMessages()
      logger.info('Creating new user...', formValues)

      const response = await apiService.createUser(formValues)

      if (response.success) {
        setSuccessMessage(response.message || 'Usuario creado exitosamente')
        reset()
        logger.info('User created successfully', response.data)
      }
    } catch (err) {
      logger.error('Failed to create user', err)
      const errorMessage = err.message || 'Error al crear el usuario'
      setServerError(errorMessage)

      if (err.message?.includes('usuario ya existe')) {
        setFieldError('usuario', 'Este usuario ya está registrado')
      }
    }
  }, [clearMessages, reset, setFieldError])

  const handleFormSubmit = async (e) => {
    e.preventDefault()
    await handleSubmit(onSubmit)
  }

  const handleInputChange = (e) => {
    const { name, value } = e.target
    handleChange(name, value)
    clearMessages()
  }

  const handleInputBlur = (e) => {
    const { name } = e.target
    handleBlur(name)
  }

  const handleReset = () => {
    reset()
    clearMessages()
  }

  return (
    <div className="agregar-container">
      <header className="agregar-header">
        <h2 className="agregar-title">Agregar Nuevo Usuario</h2>
        <p className="agregar-subtitle">
          Complete el formulario para registrar un nuevo usuario en el sistema
        </p>
      </header>

      <div className="form-card">
        {successMessage && (
          <div className="success-message" role="alert">
            <span aria-hidden="true">✅</span>
            {successMessage}
          </div>
        )}

        {serverError && (
          <div className="error-message" role="alert">
            <span aria-hidden="true">❌</span>
            {serverError}
          </div>
        )}

        <form className="user-form" onSubmit={handleFormSubmit} noValidate>
          <section className="form-section">
            <h3 className="section-title">Información Básica</h3>
            <div className="form-grid">
              <FormInput
                id="usuario"
                name="usuario"
                label="Usuario"
                value={values.usuario}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.usuario}
                touched={touched.usuario}
                placeholder="nombre_usuario"
                required
                maxLength={20}
                helpText="Solo letras, números y guiones bajos"
              />

              <FormInput
                id="nombre"
                name="nombre"
                label="Nombre"
                value={values.nombre}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.nombre}
                touched={touched.nombre}
                placeholder="Juan"
                required
                maxLength={50}
              />

              <FormInput
                id="apellido"
                name="apellido"
                label="Apellido"
                value={values.apellido}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.apellido}
                touched={touched.apellido}
                placeholder="Pérez"
                required
                maxLength={50}
              />

              <FormInput
                id="email"
                name="email"
                type="email"
                label="Email"
                value={values.email}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.email}
                touched={touched.email}
                placeholder="usuario@ejemplo.com"
                helpText="Email válido (opcional)"
              />

              <FormInput
                id="telefono"
                name="telefono"
                type="tel"
                label="Teléfono"
                value={values.telefono}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.telefono}
                touched={touched.telefono}
                placeholder="123456789"
                helpText="Solo números (9-15 dígitos)"
              />

              <FormInput
                id="especialidad"
                name="especialidad"
                label="Especialidad"
                value={values.especialidad}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.especialidad}
                touched={touched.especialidad}
                placeholder="Desarrollador Frontend"
                maxLength={50}
              />
            </div>
          </section>

          <section className="form-section">
            <h3 className="section-title">Ubicación</h3>
            <div className="form-grid">
              <FormInput
                id="ciudad"
                name="ciudad"
                label="Ciudad"
                value={values.ciudad}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.ciudad}
                touched={touched.ciudad}
                placeholder="Madrid"
                maxLength={50}
              />

              <FormTextarea
                id="direccion"
                name="direccion"
                label="Dirección"
                value={values.direccion}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.direccion}
                touched={touched.direccion}
                placeholder="Calle Ejemplo 123, Piso 2A"
                maxLength={100}
                className="full-width"
              />
            </div>
          </section>

          <section className="form-section">
            <h3 className="section-title">Redes Sociales</h3>
            <div className="form-grid">
              <FormInput
                id="instagram"
                name="instagram"
                label="Instagram"
                value={values.instagram}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.instagram}
                touched={touched.instagram}
                placeholder="@usuario"
                helpText="Incluir @ al inicio"
              />

              <FormInput
                id="facebook"
                name="facebook"
                label="Facebook"
                value={values.facebook}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.facebook}
                touched={touched.facebook}
                placeholder="facebook.com/usuario"
                helpText="URL completa de Facebook"
              />

              <FormInput
                id="x"
                name="x"
                label="X (Twitter)"
                value={values.x}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.x}
                touched={touched.x}
                placeholder="@usuario"
                helpText="Incluir @ al inicio"
              />
            </div>
          </section>

          <section className="form-section">
            <h3 className="section-title">Información Adicional</h3>
            <div className="form-grid">
              <FormInput
                id="otro1"
                name="otro1"
                label="Campo Extra 1"
                value={values.otro1}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.otro1}
                touched={touched.otro1}
                placeholder="Información adicional 1"
              />

              <FormInput
                id="otro2"
                name="otro2"
                label="Campo Extra 2"
                value={values.otro2}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.otro2}
                touched={touched.otro2}
                placeholder="Información adicional 2"
              />

              <FormInput
                id="otro3"
                name="otro3"
                label="Campo Extra 3"
                value={values.otro3}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.otro3}
                touched={touched.otro3}
                placeholder="Información adicional 3"
              />

              <FormInput
                id="otro4"
                name="otro4"
                label="Campo Extra 4"
                value={values.otro4}
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                error={errors.otro4}
                touched={touched.otro4}
                placeholder="Información adicional 4"
              />
            </div>
          </section>

          <div className="form-actions">
            <button
              type="button"
              onClick={handleReset}
              className="reset-button"
              disabled={isSubmitting}
            >
              Limpiar Formulario
            </button>

            <button
              type="submit"
              className="submit-button"
              disabled={isSubmitting}
              aria-describedby="submit-button-description"
            >
              {isSubmitting ? (
                <>
                  <div className="spinner" aria-hidden="true"></div>
                  Creando Usuario...
                </>
              ) : (
                <>
                  <span aria-hidden="true">➕</span>
                  Crear Usuario
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

export default Agregar